import Vue from 'vue'
import Router from 'vue-router'
import Wall from '@/components/Wall'
import Desk from '@/components/Desk'
import Album from '@/components/Album'
import Photo from '@/components/Photo'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'he',
      components: {
        wall: Wall,
        desk: Desk,
        album: Album,
        photo: Photo
      }
    }
  ]
})



