<template>
  <div class="main">
    <!--<div id="left_panel" >
      <div  v-if="!hideToc">
        <div class="collspseshow"><img src="./assets/btn_收起.png" style="position: fixed;cursor:pointer" @click.stop="tocHide" title="隐藏"></div>
      </div>
    </div>
    <div v-if="hideToc" class="collspsehide"><img src="./assets/btn_展开.png" style="position: fixed;cursor:pointer"  @click.stop="tocExpand" title="显示">
    </div>-->
    <div id="app" >
      <div class="wall" id = "wall" @drop="drop($event)" @dragover="allowDrop">
          <img :src="photo.url" :style="{position:'absolute',top:photo.top,left:photo.left,zIndex:photo.zindex}"  v-for="photo in photos"  draggable="true" @dragstart="dragStart($event, photo)" v-if="photo.belongto=='wall' "  @click="showphoto(photo)" :key='photo.url' />
      </div>
      <div class="desk" id = "desk"  @drop="drop($event)" @dragover="allowDrop">
        <img v-for="photo in photos" v-if="photo.belongto=='desk' " @dragstart="dragStart($event, photo)" :key='photo.url' :src="photo.url" :style="{position:'absolute',top:photo.top,left:photo.left,zIndex:photo.zindex}" @click="showphoto(photo)"/>
      </div>
      <div class="album_book"><img src="static/assets/相集.png" @click="openOrCloseAlbum"/></div>
      <div class="album" id = "album" style="background:url('static/assets/相册背景.jpg');" >
        <div style="margin: 0;padding: 0;position:absolute;left:0;top:0;width: 300px;height: 400px;" @click="change2"></div>
            <ul style="margin: 0;padding: 0;list-style-type:none;">
              <li style="background-color:white;width:300px;height:400px;position:absolute;left:300px;top:0;backface-visibility:hidden;-webkit-transform-origin: 0 50%;-webkit-transform-style: preserve-3d;" @click="change">
                <!--<img src="static/assets/测试2.png"  style="transform:rotateY(180deg);backface-visibility:hidden;"/>-->
                <img src="../src/assets/相册背景2-2.jpg"  style=""/>
              </li>
              <li style="width:300px;height:400px;position:absolute;left:300px;top:0;backface-visibility:hidden;-webkit-transform-origin: 0 50%;-webkit-transform-style: preserve-3d;" >
                <img src="../src/assets/相册背景2-1.jpg"  style="transform:rotateY(180deg);backface-visibility:hidden;"/>
                <!--<img src="static/assets/测试3.png"  style=""/>-->
              </li>
              <li style="width:300px;height:400px;position:absolute;left:300px;top:0;backface-visibility:hidden;-webkit-transform-origin: 0 50%;-webkit-transform-style: preserve-3d;" @click="change">
                <!--<img src="static/assets/测试2.png"  style="transform:rotateY(180deg);backface-visibility:hidden;"/>-->
                <img src="../src/assets/相册背景3-2.jpg"  style=""/>
              </li>
              <li style="width:300px;height:400px;position:absolute;left:300px;top:0;backface-visibility:hidden;-webkit-transform-origin: 0 50%;-webkit-transform-style: preserve-3d;">
                <img src="../src/assets/相册背景3-1.jpg"  style="transform:rotateY(180deg);backface-visibility:hidden;"/>
                <!--<img src="static/assets/测试3.png"  style=""/>-->
              </li>
              <li style="width:300px;height:400px;position:absolute;left:300px;top:0;backface-visibility:hidden;-webkit-transform-origin: 0 50%;-webkit-transform-style: preserve-3d;" @click="change">
                <!--<img src="static/assets/测试2.png"  style="transform:rotateY(180deg);backface-visibility:hidden;"/>-->
                <img src="../src/assets/相册背景.jpg"  style=""/>
              </li>
            </ul>
      </div>
      <div id="black" @click="closephoto"></div>
      <img class="photo" id = "photo">
    </div>
  </div>
</template>

<script>
/* eslint-disable no-undef */

export default {
  name: 'App',
  data () {
    return {
      hideToc: true,
      photos: [],
      album_page: 4
    }
  },
  methods: {
    change: function () {
      if (this.album_page > 1) {
        document.getElementsByTagName('li')[this.album_page].style.transform = 'rotateY(-180deg)'
        document.getElementsByTagName('li')[this.album_page - 1].style.transform = 'rotateY(-180deg)'
        document.getElementsByTagName('li')[this.album_page].style.transition = 'all 1s ease 0s'
        document.getElementsByTagName('li')[this.album_page - 1].style.transition = 'all 1s ease 0s'
        document.getElementsByTagName('li')[this.album_page].style.zIndex = 4 - this.album_page
        document.getElementsByTagName('li')[this.album_page - 1].style.zIndex = 4 - this.album_page + 1
        this.album_page -= 2
      }
    },
    change2: function () {
      if (this.album_page < 4 - 1) {
        document.getElementsByTagName('li')[this.album_page + 2].style.transform = 'rotateY(0)'
        document.getElementsByTagName('li')[this.album_page + 1].style.transform = 'rotateY(0)'
        document.getElementsByTagName('li')[this.album_page + 2].style.transition = 'all 1s ease 0s'
        document.getElementsByTagName('li')[this.album_page + 1].style.transition = 'all 1s ease 0s'
        document.getElementsByTagName('li')[this.album_page + 2].style.zIndex = this.album_page + 1
        document.getElementsByTagName('li')[this.album_page + 1].style.zIndex = this.album_page
        this.album_page += 2
      }
    },
    tocExpand () {
      this.hideToc = false
      $('#left_panel').removeClass('left_panel1')
      $('#left_panel').addClass('left_panel2')
    },
    tocHide () {
      $('#left_panel').removeClass('left_panel2')
      $('#left_panel').addClass('left_panel1')
      this.hideToc = true
    },
    showphotos: function (photos) {
      for (let i = 0; i < photos.length; i++) {
        this.addimg(photos[i].id)
      }
    },
    showphoto: function (photo) {
      let selectedPhoto = document.getElementById('photo')
      document.getElementById('black').style.display = 'block'
      selectedPhoto.style.display = 'block'
      selectedPhoto.style.width = photo.width
      selectedPhoto.style.height = photo.height
      selectedPhoto.style['margin-left'] = '-' + photo.width.substring(0, photo.width.length - 2) / 2 + 'px'
      selectedPhoto.style['margin-top'] = '-' + photo.height.substring(0, photo.height.length - 2) / 2 + 'px'
      selectedPhoto.src = photo.url
    },
    closephoto: function () {
      document.getElementById('black').style.display = 'none'
      document.getElementById('photo').style.display = 'none'
    },
    addimg: function (id) {
      let photo = this.photos[id]
      let pic = document.createElement('img')
      pic.style.top = photo.top
      pic.style.left = photo.left
      pic.style.zIndex = photo.zindex
      pic.src = photo.url
      pic.style.position = 'absolute'
      pic.addEventListener('click', showphoto)
      document.getElementById(photo.belongto).appendChild(pic)
    },
    dragStart: function (e, opts) {
      this.dragData = opts
      e.dataTransfer.setData('id', opts.id)
      e.dataTransfer.setData('originLeft', e.pageX)
      e.dataTransfer.setData('originTop', e.pageY)
    },
    allowDrop: function (e) {
      e.preventDefault()
    },
    drop: function (e) {
      let id = e.dataTransfer.getData('id')
      let originLeft = e.dataTransfer.getData('originLeft')
      let originTop = e.dataTransfer.getData('originTop')
      let index = this.findPhotoById(id)
      if (this.photos[index].belongto === e.currentTarget.id) {
        this.photos[index].left = Number(this.photos[index].left.substring(0, this.photos[index].left.length - 2)) + Number(e.pageX - originLeft) + 'px'
        this.photos[index].top = Number(this.photos[index].top.substring(0, this.photos[index].top.length - 2)) + Number(e.pageY - originTop) + 'px'
      } else {
        this.photos[index].belongto = e.currentTarget.id
        if (e.target.localName === 'img') {
          this.photos[index].left = e.target.offsetLeft + e.offsetX + 'px'
          this.photos[index].top = e.target.offsetTop + e.offsetY + 'px'
        } else {
          this.photos[index].left = e.offsetX + 'px'
          this.photos[index].top = e.offsetY + 'px'
        }
      }
      for (let i = 0; i < this.photos.length; i++) {
        if (this.photos[i].zindex > this.photos[index].zindex) {
          this.photos[i].zindex -= 1
        }
      }
      this.photos[index].zindex = this.photos.length
    },
    findPhotoById: function (id) {
      for (let i = 0; i < this.photos.length; i++) {
        if (this.photos[i].id === id) {
          return i
        } else {
          continue
        }
      }
    },
    openOrCloseAlbum: function () {
      document.getElementById('album').style.display = document.getElementById('album').style.display === 'block' ? 'none' : 'block'
    }
  },
  mounted () {
    $.get('./static/photos.json', (data, status) => {
      this.photos = data
      // this.showphotos(this.photos)
    })
  }
}
</script>

<style scoped>
  .main {
    position: absolute;
    height: 100%;
    width: 100%;
    /*background-color: rgb(196,210,227);*/
    margin:0;
    padding: 0;
    overflow: hidden;
  }
  #app {
    font-family: 'Avenir', Helvetica, Arial, sans-serif;
    background: url(../src/assets/background.png) no-repeat;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    text-align: center;
    position: absolute;
    left: calc(50% - 512px);
    width: 1024px;
    height: 600px;
  }
  .wall {
    position: relative;
    width: 50%;
    height: 60%;
    margin: 20px auto;
    overflow: hidden;
    /*background-color: red;*/
  }
  .desk {
    position: relative;
    width: 670px;
    height: 80px;
    margin: 52px auto;
    overflow: hidden;
    /*background-color: yellow;*/
    transform: perspective(0.15em) rotateX(0.33deg);
  }
  .album {
    position: absolute;
    left: 50%;
    margin-left: -300px;
    top: 20px;
    width: 600px;
    height: 400px;
    display: none;
    /*color: red;*/
    z-index: 6666;
    /*background-color: blue;*/
  }
  .album_book {
    position: absolute;
    width: 91px;
    height: 85px;
    right: 180px;
    top: 450px;
  }
  .photo {
    position: absolute;
    width: 600px;
    height: 400px;
    left: 50%;
    margin-left: -300px;
    top: 50%;
    margin-top: -200px;
    background-color: red;
    z-index: 9999;
    display: none;
  }
  #black{ width: 100%; height: 100%;  position: absolute; top:0;z-index: 9998; left: 0;background: rgba(000,000,000,0.7); display: none;}
  .collspseshow{
    width: 14px;
    float: left;
    position: absolute;
    left: 180px;
    top: calc(50% - 22px);
  }
  .collspsehide{
    width: 14px;
    height: 100%;
    top: calc(50% - 22px);
    position: absolute;
    left: -6px;
    z-index: 995;
  }
  .left_panel1 {
    width: 0;
    left: 0;
    height: 0;
    background-color: #fff;
  }
  .left_panel2 {
    z-index: 999;
    background-color: #000;

    position: absolute;
    left:0;
    width: 180px;
    height: 600px;
  }
</style>



